import { observe } from '../observer/index';
import Dep from '../observer/dep';
import Watcher from '../observer/watcher';
import { noop, warn, isPlainObject, isFunction, bind } from '../utils';

const sharedPropertyDefinition = {
    enumerable: true,
    configurable: true,
    get: noop,
    set: noop
}

//data
export function initData(vm, data) {
    if (isFunction(data)) {
        data = data()
    }
    //data get()/set()
    observe(data, true);
    vm.$data = data;
    const keys = Object.keys(data)
    let i = keys.length;
    //datavm
    while (i--) {
        proxy(vm, '$data', keys[i])
    }
}

//
const computedWatcherOptions = { lazy: true }

export function initComputed(vm, computed) {
    //user watch,render watch 
    const watchers = vm._computedWatchers = Object.create(null)
    for (const key in computed) {
        const userDef = computed[key]
        let getter = typeof userDef === 'function' ? userDef : userDef.get
        watchers[key] = new Watcher(vm, getter, noop, computedWatcherOptions)
        if (!(key in vm)) {
            //vm(get/set)
            defineComputed(vm, key, userDef)
        } else {
            warn(` "${key}" `)
        }
    }
}
//
export function initMethods(vm, methods) {
    //
    for (const key in methods) {
        vm[key] = methods[key] == null ? noop : bind(methods[key], vm)
    }
}
//
export function initWatch(vm, watch) {
    for (const key in watch) {
        const handler = watch[key];
        if (Array.isArray(handler)) {
            for (let i = 0; i < handler.length; i++) {
                createWatcher(vm, key, handler[i])
            }
        } else {
            createWatcher(vm, key, handler)
        }
    }
}
//watcher
function createWatcher(vm, key, handler) {
    let options
    //deep
    if (isPlainObject(handler)) {
        options = handler
        handler = handler.handler
    }
    // 
    if (typeof handler === 'string') {
        handler = vm[handler]
    }
    vm.$watch(key, handler, options)
}
//
function defineComputed(target, key, userDef) {
    if (typeof userDef === 'function') {
        sharedPropertyDefinition.get = createComputedGetter(key)
        sharedPropertyDefinition.set = noop
    } else {
        sharedPropertyDefinition.get = userDef.get
            ? userDef.cache !== false
                ? createComputedGetter(key)
                : userDef.get
            : noop
        sharedPropertyDefinition.set = userDef.set
            ? userDef.set
            : noop
    }
    //VMcomputed
    Object.defineProperty(target, key, sharedPropertyDefinition)
}
//get
function createComputedGetter(key) {
    return function computedGetter() {
        const watcher = this._computedWatchers && this._computedWatchers[key]
        if (watcher) {
            if (watcher.dirty) {
                watcher.evaluate()
            }
            if (Dep.target) {
                watcher.depend()
            }
            return watcher.value
        }
    }
}

// vm$datedata
function proxy(target, sourceKey, key) {
    sharedPropertyDefinition.get = function proxyGetter() {
        return this[sourceKey][key]
    }
    sharedPropertyDefinition.set = function proxySetter(val) {
        this[sourceKey][key] = val
    }
    Object.defineProperty(target, key, sharedPropertyDefinition)
}




