import HTMLParser from './html-parser'
import TextParser from './text-parser'
import codeGen from './codegen'
import { warn, camelize, isHTMLTag, isSVG } from '../utils'
import {
    dirRE,
    forAliasRE,
    forIteratorRE,
    bindRE,
    modifierRE,
    addIfCondition,
    getAndRemoveAttr,
    makeAttrsMap,
    parseModifiers,
    processIfConditions,
    setElDrictive,
    setElStyle,
    setElAttrs
} from './helpers'

//templaterender
const cache = {}

export function compileToFunctions(template, vm) {
    let root
    let currentParent
    let options = vm.$options
    let stack = [];//:push,pop()
    let hooks = vm.hooks;
    //render
    if (cache[template]) {
        return cache[template]
    }
    //John Resig  HTML Parser template to vnode(Obejctdom)
    //:http://ejohn.org/blog/pure-javascript-html-parser/
    //type 
    //1  2  3 
    HTMLParser(template, {
        //,unary:true  exp:<br/>;false  <a></a>
        start: function (tag, attrs, unary) {
            const element = {
                vm: vm,
                type: 1,
                tag,
                //[{name:key,value:value},...]
                attrsList: attrs,
                //{key1:value1,key2:value2}
                attrsMap: makeAttrsMap(attrs),//json
                parent: currentParent,
                //v-my-directive.foo.bar:arg ="expression"
                ////[{name:'my-directive',expression:'expression',modifiers:{foo:true,bar:true},arg:'arg'}]
                children: [],
                events: {},
                isComponent: !isHTMLTag(tag) && !isSVG(tag),
                nativeEvents: {},
                style: null,
                hook: {},
                props: {},//DOM
                attrs: {}//true,false
            }
            //
            setElDrictive(element, attrs);
            // setElProps(element)
            // setElAttrs(element)
            //tofix
            //
            //processFor(element) 
            //processIf(element)
            //  

            for (var hkey in hooks) {
                var hook;
                if (element[hkey] && (hook = hooks[hkey].template2Vnode)) {
                    hook(element, element[hkey], vm);
                }
            }
            //
            setElStyle(element);
            setElAttrs(element, vm.$options.delimiters);
            //
            // processKey(element)
            // processAttrs(element)

            if (!root) {
                vm.$vnode = root = element 
            }

            if (currentParent && !element.forbidden) {
                currentParent.children.push(element)
                element.parent = currentParent
            }
            //
            if (!unary) {
                currentParent = element
                stack.push(element)
            }
        },
        //
        end: function (tag) {
            const element = stack[stack.length - 1]
            const lastNode = element.children[element.children.length - 1]
            //
            if (lastNode && lastNode.type === 3 && lastNode.text === ' ') {
                element.children.pop()
            }
            // pop stack,pop
            stack.length -= 1
            currentParent = stack[stack.length - 1]
        },
        //
        chars: function (text) {
            if (!text.trim()) {
                //text = ' '
                return;
            }
            // exp: a{{b}}c => 'a'+_s(a)+'b'
            let expression = TextParser(text, options.delimiters)
            if (expression) {
                currentParent.children.push({
                    type: 2,
                    expression,
                    text
                })
            } else {
                currentParent && currentParent.children.push({
                    type: 3,
                    text
                })
            }
        }
    })
    //template
    //vnoderender 
    return (cache[template] = codeGen(root))
}






